	module.exports = {"data":{"markdownRemark":{"html":"<h2>Body Background Attribute</h2>\n<p>If you want to add a background image instead of a color, one solution is the <code>&#x3C;body background></code> attribute. It specifies a background image for an HTML document.</p>\n<p>Syntax: </p>\n<p><code>&#x3C;body background=\"URL\"></code></p>\n<p>Attribute:</p>\n<p><code>background - URL for background image</code></p>\n<p>Example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>html</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>body</span> <span class=\"token attr-name\">background</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>https://assets.digitalocean.com/blog/static/hacktoberfest-is-back/hero.png<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>body</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>html</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h2>body-background attribute is depreciated</h2>\n<p>the body-background attribute been deprecated in HTML5. The correct way to style the <code>&#x3C;body></code> tag is with CSS.</p>\n<p>There are several CSS properties used for setting the background of an element. These can be used on <body> to set the background of an entire page.</p>\n<h2>Check it Out:</h2>\n<ul>\n<li>\n<p><a href=\"https://github.com/freeCodeCamp/guides/blob/master/src/pages/css/background/index.md\">CSS background proprety</a></p>\n</li>\n</ul>","fields":{"slug":"/html/attributes/body-background-attribute/"},"frontmatter":{"title":"Body Background Attribute"}}},"pathContext":{"slug":"/html/attributes/body-background-attribute/"}}

